* {
  margin: 5;
}

.App {
  background-color: #2a2a2a;
  margin: 0;
  width: 360px;
  height: 90vh;
}

.landing {
  background-color: #2a2a2a;
  height: 90vh;
}

.landingHead {
  color: #dd7796;
  text-align: center;
  text-decoration: underline;
  position: relative;
  top: 20px;
}

.intro {
  color: #5f73bb;
  font-size: larger;
  font-weight: 600;
  font-size: 30px;
  text-align: center;
  border: 0.2rem solid #939FB7;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}



@media(max-width: 850px){
  .App{
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
  }
}

@media(max-width: 1536px){
  .App{
    width: 1440px;
  }
}

@media(min-width: 1540px) {
  .App{
    width:1920px;
  }
}


.App-logo {
  height: 3.5vmin;
  bottom: 0;
  image-rendering: crisp-edges;
  position:relative;
}


nav {
  border-bottom: 2px solid #a1a1a1;
  margin: 0;
  background-color: #2a2a2a;
  display: flexbox;
}

.navbar ol {
  list-style-type: none;
  padding-left: 0;
}

.navbarC {
  display: inline-block;
}

.navbarC a::after {
  display: inline-block;
  color: #a1a1a1;
  content: '▶︎';
  font-size: 80%;
  font-weight: bold;
  padding: 0 5px;
}


